"use client";

import React, { useState, useEffect } from "react";
import { apiRequest } from "@/app/authservice/api";
import { CheckCircle, AlertTriangle } from "lucide-react";

const ExpenseModal = ({ isOpen, onClose, onSave, editData }) => {
  const [form, setForm] = useState({
    date: "",
    category: "Other",
    description: "",
    vendor: "",
    paymentMethod: "Cash",
    paymentStatus: "Pending",
    amount: "",
    status: "Pending",
    referenceNumber: "",
    notes: "",
  });

  const [showSuccess, setShowSuccess] = useState(false);
  const [showError, setShowError] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");

  useEffect(() => {
    if (editData) setForm(editData);
  }, [editData]);

  const handleChange = (key, value) => setForm({ ...form, [key]: value });

  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      // Create FormData and append all fields
      const formData = new FormData();
      Object.entries(form).forEach(([key, value]) => {
        formData.append(key, key === "amount" ? Number(value) : value ?? "");
      });

      const response = await apiRequest("expenses/createExpense", {
        method: "POST",
        body: formData,
      });

      if (response?.success) {
        setShowSuccess(true);
        onSave(response.data); // Update parent state
        setTimeout(() => {
          setShowSuccess(false);
          onClose();
        }, 1500);
      } else {
        setErrorMessage(response?.message || "Failed to add expense");
        setShowError(true);
      }
    } catch (error) {
      console.error("Error creating expense:", error);
      setErrorMessage("Something went wrong while adding expense");
      setShowError(true);
    }
  };

  if (!isOpen) return null;

  return (
    <div className="fixed inset-0 bg-slate-900/50 backdrop-blur-sm z-[9999] flex items-center justify-center p-4">
      <div className="bg-white rounded-2xl shadow-2xl max-w-2xl w-full max-h-[90vh] overflow-y-auto">
        {/* Header */}
        <div className="sticky top-0 bg-gradient-to-r from-red-500 to-red-600 text-white p-6 rounded-t-2xl flex justify-between items-center">
          <h2 className="text-2xl font-bold">{editData ? "Edit Expense" : "New Expense"}</h2>
          <button
            onClick={onClose}
            className="p-2 px-3 hover:bg-white/20 rounded-lg transition-colors"
          >
            ✕
          </button>
        </div>

        {/* Form */}
        <form className="p-6 space-y-6" onSubmit={handleSubmit}>
          {/* ... your form fields here ... */}
          {/* Buttons */}
          <div className="flex justify-end gap-3 pt-4 border-t border-slate-200">
            <button
              type="button"
              onClick={onClose}
              className="h-9 px-4 border border-input rounded-md hover:bg-slate-100"
            >
              Cancel
            </button>
            <button
              type="submit"
              className="h-9 px-4 rounded-md text-white bg-gradient-to-r from-red-500 to-red-600 hover:from-red-600 hover:to-red-700"
            >
              {editData ? "Update Expense" : "Add Expense"}
            </button>
          </div>
        </form>
      </div>

      {/* -------- Success Modal -------- */}
      {showSuccess && (
        <div className="fixed inset-0 bg-black/40 flex items-center justify-center z-[9999]">
          <div className="bg-white p-6 rounded-xl shadow-xl text-center">
            <CheckCircle className="text-green-600 w-12 h-12 mx-auto mb-2" />
            <h2 className="text-lg font-bold text-green-700">Success</h2>
            <p className="text-gray-600">Expense added successfully</p>
          </div>
        </div>
      )}

      {/* -------- Error Modal -------- */}
      {showError && (
        <div className="fixed inset-0 bg-black/40 flex items-center justify-center z-[9999]">
          <div className="bg-white p-6 rounded-xl shadow-xl text-center">
            <AlertTriangle className="text-red-600 w-12 h-12 mx-auto mb-2" />
            <h2 className="text-lg font-bold text-red-700">Error</h2>
            <p className="text-gray-600">{errorMessage}</p>
            <button
              onClick={() => setShowError(false)}
              className="mt-4 px-4 py-2 bg-red-600 text-white rounded-md"
            >
              Close
            </button>
          </div>
        </div>
      )}
    </div>
  );
};

export default ExpenseModal;
